.oms {
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 65px 0 52px;
}

.oms-about {
  margin: 0 auto;
  width: 100%;
  max-width: 1212px;
  text-align: left;
  position: relative;

  h1 {
    margin: 0;
    font-family: $Stratos;
    font-weight: normal;
    font-size: 48px;
    line-height: 58px;
    color: $blue;
  }
}

.oms-about__list-wrapper {
  width: 1296px;
  position: relative;
  z-index: -10;

  &::after {
    position: absolute;
    right: -4px;
    top: 48px;
    display: inline-block;
    content: "";
    width: 312px;
    height: 380px;
    background-image: url("../img/card-mask.png");
    background-repeat: no-repeat;
    background-position: right;
    cursor: pointer;
  }
}

.swiper-slide {
  margin: 0 16px 0 0;
  padding: 48px 0 36px;
  cursor: pointer;
  min-width: 312px;
  max-width: 312px;
  height: 380px;


  a {
    font-family: $Stratos;
    font-size: 24px;
    line-height: 29px;
    color: $white;
    margin: 0;
    text-decoration: none;
    display: block;
    min-width: 312px;
    height: 380px;
    box-sizing: border-box;
    padding: 32px 30px 0 24px;
    border-radius: 8px;
    position: relative;
    bottom: 0;
    transition: bottom 0.3s;
  }

  &:last-child {
    margin: 0;
  }

  &:hover,
  &:focus {
    a {
      bottom: 8px;
      box-shadow: 0px 24px 32px -16px rgba(6, 6, 67, 0.2);
    }
  }
}

.swiper-slide-first {
  a {
    background-image: url("../img/oms_card-1-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-1-desktop@2x.jpg");
    }
  }
}

.swiper-slide-second {
  a {
    background-image: url("../img/oms_card-2-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-2-desktop@2x.jpg");
    }
  }
}

.swiper-slide-third {
  a {
    background-image: url("../img/oms_card-3-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-3-desktop@2x.jpg");
    }
  }
}

.swiper-slide-fourth {
  a {
    background-image: url("../img/oms_card-4-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-4-desktop@2x.jpg");
    }
  }
}

.oms-about__list-prev,
.oms-about__list-next {
  z-index: 10;
  position: absolute;
  width: 48px;
  height: 48px;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  top: 272px;
  cursor: pointer;
  outline: none;
}

.oms-about__list-prev {
  left: -62px;
  background-image: url("../img/arrow_left-icon.svg");

  &:hover {
    background-image: url("../img/arrow_left-hover-icon.svg");
  }
}

.oms-about__list-next {
  right: -62px;
  background-image: url("../img/arrow_right-icon.svg");

  &:hover {
    background-image: url("../img/arrow_right-hover-icon.svg");
  }
}

.oms-about__list-pagination {
  margin: 18px auto 0;
  width: max-content;
  max-width: 1212px;
  display: flex;
  justify-content: flex-start;
}

@media (max-width: $tablet-max-width) {
  .oms {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 36px 0 30px 31px;
  }

  .oms-about {
    margin: 0 auto;
    width: 100%;
    max-width: 1212px;
    text-align: left;

    h1 {
      margin: 0 0 10px;
      font-size: 24px;
      line-height: 24px;
    }
  }

  .oms-about__list-wrapper {
    width: 100%;
    max-width: 1249px;

    &::after {
      display: none;
    }
  }

  .swiper-slide {
    padding: 26px 0 15px;
  }

  .oms-about__list {
    width: 100%;
    max-width: 1249px;
    margin: 30px 0 0 0;
  }

  .oms-about__list-prev,
  .oms-about__list-next {
    display: none;
  }

  .oms-about__list-pagination {
    margin: 19px auto 0;
    width: max-content;
  }
}

@media (max-width: $mobile-max-width) {
  .oms {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 36px 0 30px 23px;
  }

  .oms-about {
    h1 {
      margin: 0 0 18px;
    }
  }

  .oms-about__list-wrapper {
    max-width: 608px;
  }

  .swiper-slide {
    padding: 8px 0 8px;
  }

  .oms-about__list {
    max-width: 608px;
    margin: 18px 0 0 0;
  }

  .swiper-slide {
    min-width: 225px;
    max-width: 225px;
    height: 274px;

    a {
      min-width: 225px;
      height: 274px;
    }
  }

  .swiper-slide-first {
    a {
      background-image: url("../img/oms_card-1-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-1-mobile@2x.jpg");
      }
    }
  }

  .swiper-slide-second {
    a {
      background-image: url("../img/oms_card-2-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-2-mobile@2x.jpg");
      }
    }
  }

  .swiper-slide-third {
    a {
      background-image: url("../img/oms_card-3-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-3-mobile@2x.jpg");
      }
    }
  }

  .swiper-slide-fourth {
    a {
      background-image: url("../img/oms_card-4-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-4-mobile@2x.jpg");
      }
    }
  }

  .oms-about__list-pagination {
    margin: 20px auto 0;
    width: 100%;
    max-width: 600px;
    overflow: auto;
  }
}
